@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

html {
  overflow-y: scroll;
  scroll-behavior: smooth;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

.section {
  scroll-snap-align: center;
}

h1,
p,
.typed-text-animation,
.about-text-animation,
span {
  font-family: "Orbitron", sans-serif;
}

nav a.nav-link {
  font-weight: 400;
  letter-spacing: 0.04rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.nav-link {
  color: #fff !important;
}

.bg-transparent {
  background-color: rgba(0, 0, 0, 0.61) !important;
}
.logo-png {
  width: 10rem;
}
.navbar {
  position: fixed !important;
  width: 100vw;
}

:root {
  --primary-dark: #0f0f0fe7;
  --primary-orange: #e66f00;
  --primary-white: rgba(255, 255, 255, 0.801);
  --primary-gradient: linear-gradient(360deg, #000000, #000000f6);
}

.header-wrapper {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(360deg, #000000, #000000f6);
  background: var(--primary-gradient);
  color: #e66f00;
  color: var(--primary-orange);
}

.main-info {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  padding-left: 10vw;
}

.main-info h1 {
  font-weight: 900;
  font-size: 10vw;
  z-index: 2;
}
.main-info p {
  font-size: 7vw;
  z-index: 2;
}

.typed-text-animation {
  color: rgba(255, 255, 255, 0.801);
  color: var(--primary-white);
  font-size: 3vw;
  z-index: 2;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

.particlesMobile {
  display: none;
}

/*@media only screen and (max-width: 600px) {
  .particles {
    display: none;
  }

  .particlesMobile {
    display: contents;
  }
}*/

.about-container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  background: #cf6400;
  color: rgba(255, 255, 255, 0.801);
}

.about-section-one {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 50vw;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
  z-index: 2;
}
.about-h1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  font-size: 3.5vw;
  margin-right: 1vw;
}

.change-text-color {
  color: #e66f00;
}

.about-photo {
  width: 100%;
  height: auto;
  margin-left: 6vw;
}

.about-section-two {
  display: -webkit-flex;
  display: flex;
  width: 50vw;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 5vw;
  text-align: justify;
  text-justify: inter-word;
  font-size: 1.5vw;
}

@media only screen and (max-width: 600px) {
  .about-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
  }
  .about-section-two {
    width: 100vw;
    height: 50vh;
    padding: 5vw;
    font-size: 4vw;
  }

  .about-section-one {
    width: 100vw;
    height: 50vh;
    padding-right: 5vw;
  }

  .about-h1 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: 8vw;
  }
  .about-photo {
    width: 100%;
    height: auto;
    margin-left: 14vw;
  }
}

.portfolio-container {
  width: 100vw;
  height: 100vh;
  background: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.swiper-container {
  width: 90vw;
  height: 60vh;
  margin-top: auto;
  padding-bottom: 200px;
}

.swiper-slide-prev span {
  transition: color 1s;
}

.swiper-slide-prev img {
  transition: width 1s;
}

.swiper-slide img {
  width: 27vw;
  height: auto;
  -webkit-box-reflect: below 1px
    linear-gradient(transparent, transparent, #0006);
}

.swiper-slide-active span {
  transition: color 1s;
  color: rgba(255, 255, 255, 0.801);
}

.swiper-slide-active img {
  transition: width 1s;
  width: 30vw;
}

.portfolio-h1 {
  color: rgba(255, 255, 255, 0.801);
  text-transform: uppercase;
  height: 40vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.swiper-image {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media only screen and (max-width: 1000px) {
  .swiper-slide img {
    width: 100vw;
    height: auto;
  }
}

.contact-container {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: #cf6400;
  color: rgba(255, 255, 255, 0.801);
}

.form-group {
  width: 50vw;
}

